import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
            TabBarPosition: 'bottom',
        };
    }
    render() {
        const { pathname } = this.props.location;
        console.log(pathname.split('/'));
        if(pathname.split('/').length>2){
            return null;
        }
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuche'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwuche'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan1'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan1'></i>                           
                        }
                        title="订单列表"
                        key="dingdan"
                        selected={ pathname === 'dingdan' }
                        onPress={ () => {
                            this.props.history.push('/dingdan')
                        } }
                        data-seed="logId2"
                    > 
			        </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wode1'></i> 
                        }
                        selectedIcon={
                            <i className='iconfont icon-wode1'></i>                           
                        }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === 'mytaobao' }
                        onPress={ () => {
                            this.props.history.push('/mytaobao')
                        } }
                        data-seed="logId3"
                    > 
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);